import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IManagedObject } from '@c8y/client';
import { AlertService, DroppedFile, gettext } from '@c8y/ngx-components';
import { uniqBy } from 'lodash';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { pipe } from 'rxjs';
import { map } from 'rxjs/operators';
import { RepositoryModal, RepositoryType } from '../repository.model';
import { RepositoryService } from '../repository.service';

@Component({
  selector: 'c8y-configuration-new-modal',
  templateUrl: './configuration-new-modal.component.html'
})
export class ConfigurationNewModalComponent implements RepositoryModal {
  selected: { id: string; name: string };
  versionOrName: string;
  description: string;
  binary: { file?: File; url?: string } = {
    file: undefined,
    url: undefined
  };
  deviceType: string;

  pattern: string = '';
  filterPipe;
  configs;
  mo: Partial<IManagedObject> = {};
  isLoading = false;

  result: Promise<void> = new Promise((resolve, reject) => {
    this._save = resolve;
    this._cancel = reject;
  });

  private _save;
  private _cancel;

  constructor(
    private repositoryService: RepositoryService,
    private bsModalRef: BsModalRef,
    private alert: AlertService
  ) {}

  async ngOnInit() {
    this.configs = await this.repositoryService.listConfigurations();
    this.setPipe('');
  }

  cancel() {
    this.bsModalRef.hide();
    this._cancel();
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterPipe = pipe(
      map((data: []) => uniqBy(data, 'configurationType')),
      map((data: []) => {
        return data.filter(
          (mo: any) =>
            mo.configurationType &&
            mo.configurationType.toLowerCase().indexOf(filterStr.toLowerCase()) > -1
        );
      })
    );
  }

  uploadFile(dropped: DroppedFile[], ngForm: NgForm) {
    this.binary.file = dropped[0].file;
    this.binary.url = undefined;
    ngForm.form.markAsDirty();
  }

  async save() {
    try {
      this.isLoading = true;
      const { selected, versionOrName, description, binary, deviceType } = this;
      await this.repositoryService.save(
        { selected, versionOrName, description, binary, deviceType },
        RepositoryType.CONFIGURATION,
        this.mo
      );
      this.alert.success(
        this.mo.id ? gettext('Configuration updated.') : gettext('Configuration created.')
      );
      this.bsModalRef.hide();
      this._save();
    } catch (ex) {
      this.alert.addServerFailure(ex);
      this._cancel();
    } finally {
      this.isLoading = false;
    }
  }
}
