import { Component, OnInit } from '@angular/core';
import { IManagedObject, IResultList } from '@c8y/client';
import { AlertService, gettext } from '@c8y/ngx-components';
import { Observable, pipe, Subject, timer, of } from 'rxjs';
import { debounce, distinctUntilChanged, map } from 'rxjs/operators';
import { RepositoryService } from '../repository.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ConfigurationNewModalComponent } from './configuration-new-modal.component';
import { RepositoryModal } from '../repository.model';

@Component({
  selector: 'c8y-configuration-list',
  templateUrl: './configuration-list.component.html'
})
export class ConfigurationListComponent implements OnInit {
  configurations$: Observable<IResultList<IManagedObject>>;
  filterPipe;
  filterChange$ = new Subject();
  filterTerm = '';
  private readonly CARRIAGE_RETURN_KEY = 13;
  private readonly CARRIAGE_RETURN_CODE = 'Enter';
  private readonly DELETED_SUCCESS_MSG = gettext('Configuration deleted.');

  constructor(
    private alert: AlertService,
    private repositoryService: RepositoryService,
    private modalService: BsModalService
  ) {}

  async ngOnInit() {
    await this.reset();

    this.filterChange$
      .pipe(
        debounce((event: any) =>
          event.code === this.CARRIAGE_RETURN_CODE || event.keyCode === this.CARRIAGE_RETURN_KEY
            ? timer(10)
            : timer(300)
        ),
        map((e: any) => e.target.value),
        distinctUntilChanged()
      )
      .subscribe(filterTerm => {
        this.filterTerm = filterTerm;
        this.setPipe(filterTerm);
      });
  }

  ngOnDestroy(): void {
    this.filterChange$.complete();
  }

  async add() {
    try {
      await (this.modalService.show(ConfigurationNewModalComponent, {
        class: 'modal-sm',
        ignoreBackdropClick: true
      }).content as ConfigurationNewModalComponent).result;
      await this.reset();
    } catch (ex) {
      // inteded empty
    }
  }

  async edit(configuration: IManagedObject) {
    try {
      const modal = this.modalService.show(ConfigurationNewModalComponent, {
        class: 'modal-sm',
        ignoreBackdropClick: true,
        initialState: {
          selected: { id: configuration.id, configurationType: configuration.configurationType },
          versionOrName: configuration.name,
          deviceType: configuration.deviceType,
          description: configuration.description,
          binary: { url: configuration.url }
        } as RepositoryModal
      }).content as ConfigurationNewModalComponent;
      modal.mo = configuration;
      await modal.result;
      await this.reset();
    } catch (ex) {
      // inteded empty
    }
  }

  async delete(configuration: IManagedObject) {
    try {
      await this.repositoryService.delete(configuration);
      this.alert.success(this.DELETED_SUCCESS_MSG);
      await this.reset();
    } catch (ex) {
      this.alert.addServerFailure(ex);
    }
  }

  setPipe(filterTerm: string) {
    this.filterPipe = pipe(
      map((data: []) =>
        filterTerm.trim().length === 0
          ? data
          : data.filter(
              (mo: IManagedObject) =>
                this.filterContainString(mo.name, filterTerm) ||
                this.filterContainString(mo.configurationType, filterTerm)
            )
      )
    );
  }

  private filterContainString(name: string, filterTerm: string) {
    const term = filterTerm.toLowerCase().trim();
    return name && name.toLowerCase().indexOf(term) > -1;
  }

  private async reset() {
    this.filterTerm = '';
    this.configurations$ = of(await this.repositoryService.listConfigurations());
    this.setPipe('');
  }
}
